@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/robotocondensed-regular.woff2") format("woff2"), url("../fonts/robotocondensed-regular.woff.woff") format("woff"); }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/robotocondensed-bold.woff2") format("woff2"), url("../fonts/robotocondensed-bold.woff") format("woff"); }

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/ptmono-regular.woff2") format("woff2"), url("../fonts/ptmono-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

body {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  background-color: #ffffff;
  color: #000000;
  display: grid;
  grid-template-rows: min-content 1fr min-content; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

.overflow-hidden {
  overflow: hidden; }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

.header {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%; }

.header__wrapper {
  max-width: 1491px;
  margin: 0 auto;
  padding: 0 40px;
  padding-top: 34px;
  display: flex;
  justify-content: space-between; }
  @media screen and (max-width: 1023px) {
    .header__wrapper {
      max-width: 1023px;
      padding: 0 25px; } }
  @media screen and (max-width: 767px) {
    .header__wrapper {
      max-width: 767px;
      padding: 0 16px; } }

@media (max-width: 1023px) {
  .header__wrapper {
    padding-top: 20px; } }

.logo-link {
  display: flex;
  align-items: center;
  min-width: 202px; }

.logo-link img {
  margin-right: 16px; }

.logo-link span {
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 25px;
  line-height: 29px; }

.logo-link p {
  padding: 0;
  margin: 0;
  color: #788891;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px; }

.contacts {
  display: flex; }

.contacts ul {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  align-items: center; }

.contacts__button {
  margin-left: 38px; }

.contacts li {
  margin-left: 88px; }
  .contacts li:first-child {
    margin-left: 0; }

.contacts a {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px; }
  .contacts a:hover, .contacts a:focus {
    opacity: 0.7; }

.contacts span {
  margin-left: 20px; }

@media (max-width: 1023px) {
  .contacts span {
    display: none; }
  .contacts__button {
    display: none; } }

.button-request {
  min-width: 180px;
  background: none;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 14px 13px 8px 12px;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  border-radius: 5px; }
  .button-request:hover, .button-request:focus {
    cursor: pointer;
    opacity: 0.7; }

.promo {
  padding-top: 95px;
  padding-bottom: 108px;
  background-color: #203c42;
  background-repeat: no-repeat;
  background-position: center top;
  background-size: fill;
  position: relative; }

.promo__wrapper {
  max-width: 1491px;
  margin: 0 auto;
  padding: 0 40px; }
  @media screen and (max-width: 1023px) {
    .promo__wrapper {
      max-width: 1023px;
      padding: 0 25px; } }
  @media screen and (max-width: 767px) {
    .promo__wrapper {
      max-width: 767px;
      padding: 0 16px; } }

.promo__inner {
  display: flex;
  padding: 0 calc(20% - 8px);
  padding-top: 258px;
  justify-content: space-between; }

.promo__description-promo {
  flex-basis: 393px; }

.promo__form-field {
  flex-basis: 330px; }

.footer {
  padding-bottom: 100px;
  text-align: center; }

.footer__wrapper {
  max-width: 1491px;
  margin: 0 auto;
  padding: 0 40px;
  padding: 0 240px; }
  @media screen and (max-width: 1023px) {
    .footer__wrapper {
      max-width: 1023px;
      padding: 0 25px; } }
  @media screen and (max-width: 767px) {
    .footer__wrapper {
      max-width: 767px;
      padding: 0 16px; } }
